.logo_primary-alt 	            - Primary Logo reversed for dark background situations.
.logo_condensed.max-w_1             - Smaller Mobile Friendly Logo these logos and be readable between 40px to 80px in height. 
.logo_condensed-alt.max-w_1                - Smaller Logo Reversed.

markup:
   <div class="bg_secondary-2 max-w_3"><div class="{{modifier_class}}"><span class="alt">{{modifier_class}}</span></div></div>

Styleguide DesignBase.Logos

*/
